<template>
    <div class="grid-wrap">
        <div class="grid-top">
            <div><h3>선택</h3></div>
            <div><h3>카테고리</h3></div>
            <div><h3>설문지명</h3></div>
            <div><h3>설문대상</h3></div>
            <div><h3>설문기간</h3></div>
            <div></div>
        </div>
        <div class="grid-content" v-if="list.length <= 0">
            <div>조회 된 내용이 없습니다.</div>
        </div>
        <div v-for="(item, index) in list" :key="index" class="grid-content" v-bind:style="{ 'grid-template-columns': `repeat(6, 1fr)`, 'border-bottom': `1px solid` }" v-else>
            <div><input type="checkbox" :value="item.srvMstrId" v-model="checkList"></div>
            <div>{{item.catMstrId}}</div>
            <div><router-link :to="{ name: 'detail', params: { mode:'detail', srvMstrId: item.srvMstrId}}">{{item.srvMstNm}}</router-link></div>
            <div>{{item.srvMstTg}}</div>
            <div>{{item.pbshDt}} ~ {{item.exprDt}}</div>
            <div>
                {{item.instUsrId}}
                <input type="button" value="집계하기" @click="fn_totalResponse(item.srvMstrId)"/>
                <input type="button" value="결과보기" @click="fn_showResponse(item.srvMstrId)"/>
                <input type="button" value="응답하기" @click="fn_submitResponse(item.srvMstrId)"/>
            </div>
        </div>
        <div class="grid-bottom">
            <input type="button" value="삭제" @click="fn_survey_delete"/>
            <input type="button" value="목록" @click="goBack"/>
        </div>
    </div>
</template>

<script>
export default {
  name: 'GridSpace',
  data () {
    return {
      checkList: [],
      list: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.axios.post('/action/management/list.do').then((response) => {
        let list = JSON.parse(response.data)
        this.list = list
      })
    },
    goBack () {
      this.$router.push('/')
    },
    fn_survey_delete () {
      if (this.checkList.length > 0) {
        this.axios.post('/action/management/delete.do', {value: this.checkList}).then((response) => {
          alert('삭제되었습니다.')
          this.init()
        })
      } else {
        alert('설문지를 선택해주세요')
      }
    },
    fn_totalResponse (srvMstrId) {
      this.axios.post('/action/response/srvRespTotal', {srvMstrId: srvMstrId}).then((response) => {
        const data = response.data
        if (data === '0') alert('집계가 완료되었습니다.')
        else alert('응답자가 없습니다.')
      })
    },
    fn_showResponse (srvMstrId) {
      this.$router.push({name: 'result', params: {srvMstrId: srvMstrId}})
    },
    fn_submitResponse (srvMstrId) {
      this.$router.push({name: 'response', params: {srvMstrId: srvMstrId}})
    }
  }
}
</script>

<style scoped>
 .grid-wrap > .grid-top {
     display: grid;
     grid-template-columns: repeat(6,1fr);
     grid-gap: 1em;
     text-align: center;
     background-color: darkgray
 }
 .grid-wrap > .grid-content {
     display: grid;
     grid-template-columns: 1fr;
     grid-gap: 1em;
     text-align: center;
     margin: 1em 0 1em 0;
     padding: 0 0 20px 0;
 }
 .grid-wrap > .grid-bottom {
     text-align: right;
     margin: 40px 0px 0px 0px;
 }
</style>



